

export default function stringToRack(rack) {
    var result = "";
    var numRegex = /[0-9]/;

    result = rack.replace(/[^0-9a-fA-F]*/g, "");

    if (result.length > 2) {
        result = result.substring(0,2);
    }

    if (result.length == 1) {
        return result.replace(/^(\d)+/g,"A$1").toUpperCase();
    }

    result = result.replace(/^(\d)*(\w)*/, "$2$1").toUpperCase();

    if (result.length == 1) {
        return result.replace(/^(\d)+/g,"A$1").toUpperCase();
    }

    return result;

}
